<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div class="account-order__container account-page">
    <div class="account-page__title">
      <h2>{{ $t("orderTitle") }}</h2>
    </div>
    <LoadingWrapper :loadingKeys="[getAccountOrdersKey()]">
      <div class="account-order__item-container" v-if="isReady">
        <div v-for="order in ordersList" :key="order.order_id">
          <LoadingWrapper
            :loadingKeys="[getAccountOrdersKey(order.parent_order_id)]"
          >
            <div
              :class="[
                'account-order__item cursor hover-bg',
                isShowOrders(order) && 'account-order__item-actived',
              ]"
              @click="toggleShowOrders(order)"
            >
              <div class="account-order__left-section">
                <div class="account-order__item-left">
                  <router-link
                    :to="{
                      path: `orders/view/id/${order.parent_order_id}`,
                    }"
                    class="account-order__item-img"
                  >
                    <ImageViewer
                      :src="image(order.image)"
                      class="img img-responsive"
                      :alt="order.name"
                      loading="lazy"
                    />
                  </router-link>
                  <div
                    @click="event => viewDetail(order, event)"
                    class="account-order__item-content"
                  >
                    <h2 class="account-order__item-name">
                      {{ order.name }}
                    </h2>

                    <div class="account-order__item-subcontent">
                      <span># {{ order.order_id }}</span>
                      <span>{{ order.qty }} {{ $t("quantity") }}</span>
                      <span>{{ order.order_date | formatDateTime }}</span>
                    </div>
                  </div>
                </div>
                <div class="account-order__item-right">
                  <div class="account-order__item-total">
                    <span class="account-order__item-name">{{
                      parseInt(order.order_total) | formatISK
                    }}</span>
                  </div>
                  <span
                    :class="[
                      'account-order__item-status',
                      `color__status--${statusColor(order.order_status)}`,
                    ]"
                    >{{ order.order_status }}</span
                  >
                </div>
              </div>
              <div
                :class="[
                  'icon--custom',
                  isShowOrders(order) && 'icon--arrow-down--rolate',
                ]"
              >
                <i class="icon--arrow-down" />
              </div>
            </div>
          </LoadingWrapper>
          <div
            v-if="isShowOrders(order) && subOrdersMap[order.parent_order_id]"
          >
            <div
              v-for="subOrder in subOrdersMap[order.parent_order_id]"
              :key="subOrder.item_id"
              class="account-order__item account-order__item-actived"
            >
              <span
                class="account-order__item-subname account-order__item-name--child cursor"
                >{{ subOrder.name }}</span
              >
              <span
                class="account-order__item-name account-order__item-name--child"
                >{{ +subOrder.row_total_incl_tax | formatISK }}</span
              >
            </div>
          </div>
        </div>
        <div class="account-order__pagination">
          <Pagination
            v-bind="paginationProps"
            :showPagination="getOrdersList"
          />
        </div>
      </div>

      <div v-else-if="!isDataReady" class="account-order__item-container" />

      <div v-else>
        <img
          alt="no order image"
          class="noOrdersImage"
          src="../../assets/svg/Shopping.svg"
        />
        <span class="noOrdersText">
          <h1>{{ $t("noOrderText") }}</h1>
        </span>
        <span class="noOrdersSubtext">
          <p>{{ $t("noOrderSubtext") }}</p>
        </span>
        <div class="gotoshop-btn__wrap">
          <router-link to="/">
            <base-button class="primary">{{ $t("gotoStore") }}</base-button>
          </router-link>
        </div>
      </div>
    </LoadingWrapper>
  </div>
</template>

<i18n>
{
  "is": {
    "gotoStore": "Fara í verslun",
    "noOrderText": "Þú átt engar fyrri pantanir",
    "noOrderSubtext": "Er ekki kominn tími á að versla?",
    "quantity": "fjöldi",
    "orderTitle": "Pantanir"
  },
  "en": {
    "gotoStore": "Start Shopping Now",
    "noOrderText": "You currently have no previous orders",
    "noOrderSubtext": "Best get shopping with Aha",
    "quantity": "items",
    "orderTitle": "My Orders"
  }
}
</i18n>

<script>
import { mapGetters, mapActions } from "vuex";

import BaseButton from "@/components/BaseButton";
import LoadingWrapper from "@/components/LoadingWrapper";
import Pagination from "@/components/Pagination";
import ImageViewer from "@/components/ImageViewer";

import { getOrderInfo, getOrdersList } from "@/services/order";

import get from "@/util/get";
import getStatusColor from "@/util/getStatusColor";

export default {
  name: "ViewMyAccountOrders",
  components: {
    BaseButton,
    LoadingWrapper,
    Pagination,
    ImageViewer,
  },
  data() {
    return {
      showItemInOrders: [],
      subOrdersMap: {},
      orders: undefined,
      isDataReady: false,
    };
  },
  computed: {
    ...mapGetters(["getAccountOrdersKey"]),
    isReady() {
      const { ordersList, ordersTotal } = this;
      return ordersList && ordersTotal ? true : false;
    },
    isHaveOrders() {
      return this.ordersTotal > 0;
    },
    paginationProps() {
      return {
        totalItems: this.ordersTotal,
        showEntries: 10,
      };
    },
    ordersList() {
      return get(this, "orders.ordersList");
    },
    ordersTotal() {
      return get(this, "orders.totalCount", 0);
    },
  },
  methods: {
    ...mapActions(["toggleLoading"]),
    isShowOrders(order) {
      return this.showItemInOrders.includes(order.order_id);
    },
    statusColor(status) {
      return getStatusColor(status);
    },
    pushItemInOrders(orderId) {
      this.showItemInOrders.push(orderId);
    },
    sliceItemInOrders(orderId) {
      this.showItemInOrders = this.showItemInOrders.filter(o => o !== orderId);
    },
    toggleShowOrders(order) {
      const {
        showItemInOrders,
        sliceItemInOrders,
        pushItemInOrders,
        toggleLoading,
        getAccountOrdersKey,
      } = this;
      const { order_id, parent_order_id } = order;
      showItemInOrders.includes(order_id)
        ? sliceItemInOrders(order_id)
        : pushItemInOrders(order_id);
      if (!this.subOrdersMap[parent_order_id]) {
        toggleLoading({
          loadingKey: getAccountOrdersKey(parent_order_id),
          isLoading: true,
        });
        this.getOrderInfo(parent_order_id);
      }
    },
    async getOrderInfo(parentOrderId) {
      const { subOrdersMap, toggleLoading, getAccountOrdersKey } = this;
      const ordersInfo = await getOrderInfo(parentOrderId);
      const ordersItem = get(
        ordersInfo,
        `data.suborders.${[parentOrderId]}.items`,
        []
      );
      this.subOrdersMap = {
        ...subOrdersMap,
        [parentOrderId]: Object.values(ordersItem),
      };
      toggleLoading({
        loadingKey: getAccountOrdersKey(parentOrderId),
        isLoading: false,
      });
    },
    async getOrdersList(skip, limit) {
      const { toggleLoading, getAccountOrdersKey } = this;
      toggleLoading({
        loadingKey: getAccountOrdersKey(),
        isLoading: true,
      });

      const data = await getOrdersList({ skip, limit });
      let ordersList = [],
        totalCount = get(data, "total_count.0", 0);
      Object.values(get(data, "records.0", [])).forEach(item => {
        const orderId = Object.keys(item)[0],
          orderItem = Object.values(item)[0];
        ordersList.push({
          ...orderItem,
          parent_order_id: orderId,
        });
      });

      this.orders = {
        ordersList,
        totalCount,
      };

      toggleLoading({
        loadingKey: getAccountOrdersKey(),
        isLoading: false,
      });
    },
    viewDetail(order, e) {
      e.stopPropagation();
      this.$router.push(`orders/view/id/${order.parent_order_id}`);
    },
    image(img) {
      if (img === "no_selection" || !img) return "";
      return img;
    },
  },
  created() {
    this.isDataReady = false;
  },
  async mounted() {
    const { ordersList, getOrdersList } = this;
    if (!ordersList) {
      const skip = Number(get(this.$route, "query.page", 1));
      await getOrdersList(skip, 20);
    }
    this.isDataReady = true;
  },
};
</script>

